@media (min-width: 500px) {
  /* blog */
  .article {
    flex-direction: row;
  }

  .article-image,
.article-title {
    width: 50%;
  }

  .article-title {
    align-items: center;
  }

  /* guarantee-page */
  .key-wrapper {
    margin-top: 80px;
  }

  .key-text {
    padding: 30px;
  }

  /* certificates */
  .certificate {
    width: calc((100% - 10px) / 2);
    margin-bottom: 10px;
    margin-right: 10px;
  }
  .certificate:nth-child(2n) {
    margin-right: 0;
  }
}